/**
 * 格式化后的 router
 */
import Home from '../views/Home.vue';

// base
import Login from '../views/Login.vue';
import Register from '../views/Register.vue';
import FindPassword from '../views/FindPassword.vue';

// 动态
import feed from './feed/feed.router';
// 我
import mine from './mine/mine.router';
// 圈子
import group from './group/group.router';

import other from './other/other.router';

const base = [
    {path: '/feeds', redirect: '/feeds/new', meta: { title: '动态' } },
    { path: '/', redirect: '/home', meta: { title: 'Ealing' } },
    {
      path: '/home',
      component: Home,
      name: 'home',
      meta: {
        title: 'home',
        canEnterOrNot: true
      }
    }, {
        path: '/login',
        component: Login,
        name: 'login',
        meta: {
            title: 'login',
            canEnterOrNot: true
        }
    }, {
        path: '/register',
        component: Register,
        name: 'register',
        meta: {
            title: '注册',
            canEnterOrNot: true
        }
    }, {
        name: 'forgot',
        path: '/forgot',
        component: FindPassword,
        meta: {
            title: '找回密码',
            canEnterOrNot: true
        }
    }
];

const routes = [
    ...base,
    ...feed,
    ...group,
    ...mine,
    ...other
];

export default routes;



