<template>
    <div class="home-container">
      <div class="weather-container">
        <div style="height:120px" v-show="isWeatherShow">这里展示天气信息，点击进入天气专题页面，下拉后隐藏掉天气版块，进入推荐首页数据</div>
        <div v-show="isWeatherShow">
          <mt-loadmore
            :top-method="loadTop"
            topPullText=""
            topDropText=""
            @translate-change = "translateChange"
            @top-status-change="handleTopChange"
            ref="loadmore">
            <div>&nbsp;</div>
          </mt-loadmore>
        </div>

        <HomeMenu v-show="isMenuShow" v-bind:style="{transform: translate }"/>
        <div v-show="isFeedNavShow">推荐数据导航</div>
        <div class="feeds-main-content" v-bind:style="{transform: translate }">
            <div>推荐内容，动态、资讯、问答、圈子...(列表形式)</div>
            <div>推荐内容，动态、资讯、问答、圈子...(列表形式)</div>
            <div>推荐内容，动态、资讯、问答、圈子...(列表形式)</div>
            <div>推荐内容，动态、资讯、问答、圈子...(列表形式)</div>
        </div>
      </div>

      <ToolBar/>
    </div>
</template>
<script>
import HomeMenu from '../components/homeMenu';
import ToolBar from '../components/ToolBar';

export default {
    name: 'home',
    components: {
        HomeMenu,
        ToolBar
    },
    data () {
      return {
        seoTitle: this.$t('lang.appName'),
        isWeiXin: window.EA_WEB.isWeiXin,
        translate: "translate3d(0px, 0px, 0px)",
        isMenuShow: true,
        isWeatherShow: true,
        isFeedNavShow: false
      }
    },
    created: function () {
      document.title = this.$t('lang.'+this.$route.meta.title);
      this.$nextTick(function () {
        this.loadFeeds();
      })
    },
    methods: {
      loadFeeds() {
        setTimeout(() => {
          this.$store.commit('HIDELOADING');
          // this.$router.push({path: '/login'})
        }, 2000);
      },
      loadTop() {
        // load more data
        this.$refs.loadmore.onTopLoaded();
      },
      handleTopChange(status) {
        if(status == "loading") {
          this.isMenuShow = false;
          this.isWeatherShow = false;
          this.isFeedNavShow = true;
        }
      },
      translateChange(translate) {
        if(translate < 2) translate = 0;
        
        this.translate = "translate3d(0px, "+translate+"px, 0px)";
      }
    }
}
</script>
<style lang="less">
.home-container {
  height: 100%;
}
.weather-container {
  height: auto;
  width: 100%;
  background-color: #1dace6;
}
.mint-loadmore {
  width: 100%;
  overflow: initial;
  position: absolute;
  top:0px;
  bottom:54px;
  z-index: 99;
}
.feeds-main-content {
  overflow: auto;
  padding: 46px 0 54px 0;
  height: 100%;
  background-color: #f4f5f5;
}
</style>



