<template>
  <div id="app">
    <router-view/>
    <Loading v-show="showLoading"></Loading>


    <FeedMoreAction />
    <PinnedPop />

    <NoticeText/>
    <IviewSwiper/>
    <PrePost :config="prePostConfig" />
    <PostFeed/>
    <Confirm />
    <postQuestion />
    <PostAnswer />
    <CheckinLayer/>
  </div>
</template>

<script>
import './config/global';
import Loading from './components/Loading';
import {mapGetters} from 'vuex';

import NoticeText from './components/Notice';
import IviewSwiper from './components/IviewSwiper';
import PrePost from './components/PrePost';
import PostFeed from './components/PostFeed';
import CommentInput from './components/CommentInput';
import FeedDiggList from './components/FeedDiggList';
import Confirm from './components/Confirm';
import postQuestion from './components/postQuestion';
import PostAnswer from './components/PostAnswer';
import CheckinLayer from './components/CheckinLayer';

import FeedMoreAction from './components/FeedMoreAction';
import PinnedPop from './components/PinnedPop';

export default {
  name: 'app',
  components: {
    Loading,
    NoticeText,
    IviewSwiper,
    PostFeed,
    FeedDiggList,
    CommentInput,
    Confirm,
    PrePost,
    postQuestion,
    PostAnswer,
    FeedMoreAction,
    PinnedPop,
    CheckinLayer
  },
  data: () => ({
    prePostConfig: {
      // 签到应用状态
      checkin: {
        status: true,
        amount: 0,
      }
    },
  }),
  computed: {
    ...mapGetters([
      'showLoading'
    ])
  }
}
</script>

<style lang="less">
  @import './styles/font.less';
</style>



