window.EA_WEB = {
  'api': 'http://api.spider.com',
  'apiv1': '',
  'url': '',
  'im_token': '',
  'webSocket': null,
  'loaded': false,
  'isWeiXin': false,
  'currentUserId': 0,
  'dataBase': null,
  'goldName': '金币'
};

// 判断微信浏览器
function isWeiXin() {
  var ua = window.navigator.userAgent.toLowerCase();
  if (ua.match(/MicroMessenger/i) == 'micromessenger') {
  return true;
} else {
  return false;
  }
}

if(isWeiXin()) {
  EA_WEB.isWeiXin = true;
}



