  <ul class="cate-wrap bbOnepx">
    <li><a class="icon" href="" tongji_tag="m_home_job_new"><span class="cate-img" id="job"></span><span class="cate-desc">招聘</span></a></li>
    <li><a class="icon" href="" tongji_tag="m_home_house_new"><span class="cate-img" id="house"></span><span class="cate-desc">房产</span></a></li>
    <li><a class="icon" href="" tongji_tag="m_home_car_new"><span class="cate-img" id="car"></span><span class="cate-desc">二手车</span></a></li>
    <li><a class="icon" href="" tongji_tag="m_home_sale_new"><span class="cate-img" id="sale"></span><span class="cate-desc">二手</span></a></li>
    <li><a class="icon" href="" tongji_tag="m_home_pets_new"><span class="cate-img" id="pets"></span><span class="cate-desc">宠物</span></a></li>
    <li><a class="icon" href="" tongji_tag="m_home_jianzhi_new"><span class="cate-img" id="jianzhi"></span><span class="cate-desc">兼职</span></a></li>
