      width: 38px;
      height: 38px;
      background-size: 208px auto;
      display: inline-block;
  }
  .cate-desc {
      display: block;
      padding-top: 2px;
  }
  #job {position: relative;}
  #house{background-position:-42px 0}
  #car{background-position:-84px 0}
  #sale{background-position:-126px 0}
  #pets{background-position:-167px 0}
  #jianzhi{background-position:0 -42px}
  #shenghuo{background-position:-42px -42px}
  #jiazheng{background-position:-84px -42px}
  #gold{background-position:-126px -42px;position:relative}
  #task{background-position:-167px -42px;position:relative}
</style>

<script>
  const homeMenu = {
    name: 'homeMenu',
    data: () => ({
    }),
  }

  export default homeMenu;
</script>



