import { SHOWLOADING,HIDELOADING } from '../types';

const state = {
    showLoading:true
};

const mutations = {
    [SHOWLOADING](state){
        state.showLoading = true;
    },
    [HIDELOADING](state){
        state.showLoading = false;
    }
};

const actions = {
  [SHOWLOADING] (context, cb) {
    cb ( (options) => {
      context.commit(SHOWLOADING, options);
    })
  },
  [HIDELOADING] (context, cb) {
    cb ( (options) => {
      context.commit(HIDELOADING, options);
    })
  }
};

const getters = {
    showLoading(state){
        return state.showLoading
    }
};

const store = {
  state,
  mutations,
  actions,
  getters
};

export default store;



