// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import baseLang from './language/base'
import UserAvatar from './components/user/UserAvatar'
import store from './stores/store.js'

//  加入UI库
import 'mint-ui/lib/style.css';
import iView from 'iview';
import './styles/common.less';
import { TabContainer, TabContainerItem, Loadmore, Popup, Picker } from 'mint-ui';
Vue.use(iView);

// 本地存储插件
import storeLocal from 'store';

const components = [
  Popup,
  Picker,
  Loadmore,
  UserAvatar,
  TabContainer,
  TabContainerItem
];

components.map(component =>{
  Vue.component(component.name, component);
});

// 注册一个全局自定义指令 v-focus
Vue.directive('focus', {
  // 当绑定元素插入到 DOM 中。
  inserted: function (el) {
    // 聚焦元素
    el.focus();
  }
});

// 注册一个全局自定义指令 v-focus
Vue.directive('childfocus', {
  // 当绑定元素插入到 DOM 中。
  inserted: function (el) {
    // 聚焦元素
    el.firstChild.focus();
  }
});

Vue.directive('fixed', {
  bind: (el, binding, vnode) => {
    const ot = el.offsetTop;
    document.onscroll = () => {
      const st = document.body.scrollTop || document.documentElement.scrollTop;
      el.setAttribute('data-fixed', st > ot ? 'fixed': '');
    };
  }
});

//  加入多语言配置
import VueI18n from 'vue-i18n'
Vue.use(VueI18n)

Vue.config.productionTip = false

// 创建一个 i18n 实例
const i18n = new VueI18n({
  locale: 'cn',
  messages: baseLang
})

// 定义 localStorage 组件
Vue.prototype.$storeLocal = storeLocal;

const app = new Vue({
  el: '#app',
  router,
  store,
  i18n,
  render: h => h(App)
});

// iview 全局消息提示默认配置
app.$Message.config({top: 8, duration: 3});

export { app, router };
