const Feeds = () =>
    import('../../views/Feeds');
const FeedDetail = () =>
    import('../../views/FeedDetail.vue');

export default [{
    name: 'feed',
    path: '/feeds/:type',
    component: Feeds,
    meta: {
        title: 'feeds'
    }
}, {
    name: 'feed-detail',
    path: '/feed-detail/:feedID',
    component: FeedDetail
}];



